import React from 'react'
import Helmet from 'react-helmet'
import Link from 'gatsby-link'

const Project2 = () => (
  <div className="page Project2-page">
    <Helmet
      title="Lans-fm Project, Kacper Cieciński Junior Web Developer"
      meta={[
        { name: 'description', content: 'Lans-fm project by Kacper Ciecinski' },
        { name: 'keywords', content: 'Kacper, Cieciński, Kacper Cieciński, Ciecinski, developer, junior developer,junior react developer, junior front-end developer,front-end,frontend,backend,junior backend developer,junior rails developer,React,Ruby on Rails,Javascript, HTML5, CSS, internship, BinarApps intern, BinarApps internship, React Intern' },
      ]}
    />

    <h1>Lans-Fm</h1>
    <div className="row row-center">
      <div className="col-md-10">
      <p>
        Project I made on summer internship in BinarApps, during this internship we worked in teams of 3 peaople and developed an React App. <br/>
        It was my first project made in cooperation with other people and it show me how real project development look like, as we have daily stand-ups and one of senior developers being our "client" <br/>
        I learned many soft skills like team work and how to manage a project with tools like trello and git. <br/>
        On technical side it really improved my React knowladge and whole React ecosystem, we used tools like Redux, redux-thunk, react-router. <br/>
        Our app was using Last Fm api and was used to search for artist similar to provided one, also with some basic spotify integration you could listen to artist you found and also scrobble them on last fm. <br/>
      </p>
      <div className="info-container">
        <p>
          <a href=""><i className="fa fa-th-large"></i>No Live Demo</a>
          <a target="_blank" href="https://github.com/marzena-badziak/lans-fm"><i className="fa fa-github"></i>Github</a>
        </p>
      </div>
      <Link className="project-link" to="/projects">Back to projects</Link>

      </div>
    </div>

  </div>
)

export default Project2


